(function(){
    const roles = document.querySelector('#rol')

    if(roles) {
        const rol = document.querySelector('[name="rolid"]');
        const inputHiddenRol = document.querySelector('[name="rol_id"]');
        let busqueda= {
            rolid: +rol.value
        }

        rol.addEventListener('change', terminoBusqueda)
        function terminoBusqueda(e) {
            busqueda[e.target.name] = e.target.value;
            inputHiddenRol.value = e.target.value;
        }


    }

})();